


































































































.card-content {
  width: 100%;
  position: relative;
  padding-bottom: 52px;
  word-break: break-all;
  word-break: break-word;
  overflow: hidden;
  .md-subhead {
    .md-icon {
      $size: 16px;

      width: $size;
      min-width: $size;
      height: $size;
      min-height: $size;
      font-size: $size;
      line-height: $size;
    }

    span {
      vertical-align: middle;
    }
  }
  .md-card-actions {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    .providerName {
      position: absolute;
      left: 16px;
    }
  }
}
